        <el-button type="success" disabled></el-button>
        <el-button type="info" disabled></el-button>
        <el-button type="warning" disabled></el-button>
        <el-button type="danger" disabled></el-button>
      </el-row>

      <el-row>
        <el-button plain disabled></el-button>
        <el-button type="primary" plain disabled></el-button>
        <el-button type="success" plain disabled></el-button>
        <el-button type="info" plain disabled></el-button>
        <el-button type="warning" plain disabled></el-button>
        <el-button type="danger" plain disabled></el-button>
