        <el-button type="primary" icon="el-icon-share"></el-button>
        <el-button type="primary" icon="el-icon-delete"></el-button>
      </el-button-group>
      <h1></h1>
      <el-button type="primary" :loading="true"></el-button>
      <h1></h1>
      <el-row>
        <el-button></el-button>
        <el-button size="medium"></el-button>
        <el-button size="small"></el-button>
        <el-button size="mini"></el-button>
      </el-row>
      <el-row>
        <el-button round></el-button>
        <el-button size="medium" round></el-button>
        <el-button size="small" round></el-button>
        <el-button size="mini" round></el-button>
      </el-row>
      <h1></h1>
    </div>
</template>

<script>
export default {
  name: 'button'
}
</script>

<style scoped>

</style>



